html {
  scroll-behavior: smooth;
}

*, *::before, *::after {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

input, select, button, textarea {
  font: inherit;
}

img, svg, picture, video {
  display: block;
  max-width: 100%;
}

ul, ol {
  list-style: none;
}

body {
  min-height: 100vh;
}

body {
  font-family: "Hanken Grotesk", sans-serif;
  font-size: 1.125rem;
}
@media (min-width: 40rem) {
  body {
    font-size: 1rem;
  }
}

h2 {
  font-size: 1.5rem;
}

h3 {
  font-size: 2.60625rem;
}
@media (min-width: 40rem) {
  h3 {
    font-size: 1.954375rem;
  }
}

a {
  text-decoration: none;
}